  float: left;
  height: 0.64rem;
  overflow: hidden;
  -webkit-transition: background-color .15s;
  transition: background-color .15s;
  width: 0.64rem;
}

.viewer-toolbar li:hover {
  background-color: rgba(0, 0, 0, .8);
}

.viewer-toolbar li::before {
  margin: 0.053333rem;
}

.viewer-toolbar li + li {
  margin-left: 0.026667rem;
}

.viewer-toolbar .viewer-small {
  height: 0.48rem;
  margin-bottom: 0.08rem;
  margin-top: 0.08rem;
  width: 0.48rem;
}

.viewer-toolbar .viewer-small::before {
  margin: -0.026667rem;
}

.viewer-toolbar .viewer-large {
  height: 0.8rem;
  margin-bottom: -0.08rem;
  margin-top: -0.08rem;
  width: 0.8rem;
}

.viewer-toolbar .viewer-large::before {
  margin: 0.133333rem;
}

.viewer-tooltip {
  background-color: rgba(0, 0, 0, .8);
  border-radius: 0.266667rem;
  color: #fff;
  display: none;
  font-size: 0.32rem;
  height: 0.533333rem;
  left: 50%;
  line-height: 0.533333rem;
  margin-left: -0.666667rem;
  margin-top: -0.266667rem;
  position: absolute;
  text-align: center;
  top: 50%;
  width: 1.333333rem;
}

.viewer-title {
  color: #ccc;
  display: inline-block;
  font-size: 0.32rem;
  line-height: 1;
  margin: 0 5% 0.133333rem;
  max-width: 90%;
  opacity: .8;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-transition: opacity .15s;
  transition: opacity .15s;
  white-space: nowrap;
}

.viewer-title:hover {
  opacity: 1;
}

.viewer-button {
  background-color: rgba(0, 0, 0, .5);
  border-radius: 50%;
  cursor: pointer;
  height: 2.133333rem;
  overflow: hidden;
  position: absolute;
  right: -1.066667rem;
  top: -1.066667rem;
  -webkit-transition: background-color .15s;
  transition: background-color .15s;
  width: 2.133333rem;
}

.viewer-button:focus,
.viewer-button:hover {
  background-color: rgba(0, 0, 0, .8);
}

.viewer-button::before {
  bottom: 0.4rem;
  left: 0.4rem;
  position: absolute;
}

.viewer-fixed {
  position: fixed;
}

.viewer-open {
  overflow: hidden;
}

.viewer-show {
  display: block;
}

.viewer-hide {
  display: none;
}

.viewer-backdrop {
  background-color: rgba(0, 0, 0, .5);
}

.viewer-invisible {
  visibility: hidden;
}

.viewer-move {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.viewer-fade {
  opacity: 0;
}

.viewer-in {
  opacity: 1;
}

.viewer-transition {
  -webkit-transition: all .3s;
  transition: all .3s;
}

@-webkit-keyframes viewer-spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes viewer-spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.viewer-loading::after {
  -webkit-animation: viewer-spinner 1s linear infinite;
  animation: viewer-spinner 1s linear infinite;
  border: 0.106667rem solid rgba(255, 255, 255, .1);
  border-left-color: rgba(255, 255, 255, .5);
  border-radius: 50%;
  content: '';
  display: inline-block;
  height: 1.066667rem;
  left: 50%;
  margin-left: -0.533333rem;
  margin-top: -0.533333rem;
  position: absolute;
  top: 50%;
  width: 1.066667rem;
  z-index: 1;
}

@media (max-width: 767px) {
  .viewer-hide-xs-down {
    display: none;
  }
}

@media (max-width: 991px) {
  .viewer-hide-sm-down {
    display: none;
  }
}

@media (max-width: 1199px) {
  .viewer-hide-md-down {
    display: none;
  }
}
